<template>
  <el-pagination
    v-if="total"
    style="text-align: center;margin-top: 15px"
    @size-change="val => handlePagination('size', val)"
    @current-change="val => handlePagination('page', val)"
    :current-page="pages.pageNum"
    :page-size="pages.pageSize"
    :page-sizes="pagesConfig.pageSizes"
    :layout="pagesConfig.layout"
    :total="total" />
</template>
<script>
  export default {
    name: 'memberPagination',
    props: {
      pagesConfig: {
        type: Object,
        default: () => ({pageSizes: [10,20,50],layout: "total, sizes, prev, pager, next, jumper"})
      },
      total: {
        type: Number,
        default: () => 0
      },
      pages: {
        type: Object,
        default: () => ({pageNum:1, pageSize:10})
      }

    },
    methods: {
      handlePagination(type, val) {
        let _this = this
        let _pages = {pageNum:1,pageSize:10}
        if (type == 'size') {
          _pages.pageSize = val
        } else if(type == 'page') {
          _pages.pageNum = val
        }
        _this.$emit('callback', 'pagination', _pages)
      },
    },
  }
</script>
