<template>
  <div class="member-box">
    <div class="query-wrapper">
      <div>
        <el-form :inline="true" :model="queryForm" ref="queryForm" label-width="100px">
          <el-form-item label="部门名称">
            <el-input v-model="queryForm.phone" :placeholder="'input' | solvePlaceholder" />
          </el-form-item>
          <el-form-item label="状态">
            <el-input v-model="queryForm.enterMan" :placeholder="'input' | solvePlaceholder" />
          </el-form-item>
        </el-form>
      </div>
      <div class="query-btn-group mt15">
        <el-button type="primary" @click="query">查询</el-button>
        <el-button @click="reset">重置</el-button>
        <el-button @click="add">新增</el-button>
      </div>
    </div>
    <el-table :data="departs">
      <el-table-column prop="userName" label="部门名称" />
      <el-table-column prop="userName1" label="负责人" />
      <el-table-column prop="num" label="状态" />
      <el-table-column prop="result" label="创建时间" />
      <el-table-column prop="result" label="操作" />
    </el-table>

    <MemberPagination @callback="queryContent" :total="total" :pages="pages"/>
    <el-dialog title="修改或添加" :visible.sync="dialogFormVisible">
      <el-form :model="departForm" ref="departForm">
        <el-form-item label="上级部门">
          <el-input :placeholder="'input' | solvePlaceholder" />
        </el-form-item>
        <el-form-item label="部门名称">
          <el-input :placeholder="'input' | solvePlaceholder" />
        </el-form-item>
        <el-form-item label="联系电话">
          <el-input :placeholder="'input' | solvePlaceholder" />
        </el-form-item>
        <el-form-item label="负责人">
          <el-input :placeholder="'input' | solvePlaceholder" />
        </el-form-item>
        <el-form-item label="部门状态">
          <el-input :placeholder="'input' | solvePlaceholder" />
        </el-form-item>
      </el-form>

      <span slot="footer">
        <el-button type="primary">保存</el-button>
        <el-button @click="e=>dialogFormVisible=false">取消</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
  import {Apis} from '@/axios'
  import MemberPagination from '@/components/memberPagination'

  export default {
    name: 'Departments',
    data() {
      return {
        queryForm: {
          phone:'',
          enterMan:'',
          userName:'',
          myBriefing:''
        },
        queryFormCopy:{},
        departs: [],
        dialogFormVisible: false,
        departForm:{},
        pages: {
          pageNum: 1,
          pageSize: 10
        },
        total: null
      }
    },
    components:{MemberPagination},
    mounted() {
      let _this = this
      _this.init()
    },
    methods: {
      init() {
        let _this = this
        _this.query()
      },
      query() {
        let _this = this
        _this.queryContent('query')
      },
      handlePagination(type, val) {
        let _this = this
        if (type == 'size') {
          _this.pages.pageSize = val
        } else if(type == 'page') {
          _this.pages.pageNum = val
        }
        _this.queryContent('pagination')
      },
      queryContent(type, _pages) {
        let _this = this
        let {deptPage} = Apis.member
        let params = type == 'size' ? {..._this.queryForm} : type == 'pagination' ? {..._this.queryFormCopy}: {}
        params = _pages ? {...params, ..._pages} : {...params, ..._this.pages}
        deptPage(params).then(res => {
          if (res.data.status == 200) {
            let {records, total} = res.data.data
            _this.total = total
            _this.departs = records
          } else _this.$message.error(res.data.msg)
        })
      },
      reset() {},
      add() {
        let _this = this
        _this.dialogFormVisible = true
      }
    }
  }
</script>
<style lang="less" scoped>
  .member-box {
    height: 100%;
    overflow-y: auto;
    box-sizing: border-box;
    .query-wrapper{
      margin-bottom: 30px;
    }
    .query-btn-group{
      text-align: right;
    }
  }
</style>
